/* MusicPlayer.css */
body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    overflow: hidden;
  }
  .App {
    text-align: center;
  }
  
  .heading {
    background-color: #181817; /* Background color behind the heading text */
    color: #e8afaf; /* Text color of the heading */
    padding: 10px;
    /*border-radius: 10px;
    /*display: inline-block; /* Adjust the width to fit the text */
  }
  
  .music-player-container {
    background-image: url('./assets/background.webp');
    background-size: cover;
    background-position: center;
    height: 700px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .music-player {
    background-color: rgba(93, 92, 92, 0.8);
    padding: 20px;
    width: 200px;
    height: 400px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .album-art {
    width: 100px;
    height: 100px;
    border-radius: 10px;
    margin-bottom: 20px;
    animation: rotate 10s linear infinite;
  }
  
  @keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .track-details {
    text-align: center;
  }
  
  .track-details h3 {
    margin: 0;
    font-size: 1.2em;
  }
  
  .track-details p {
    margin: 0;
  }
  
  .progress-bar {
    width: 100%;
    height: 10px;
    background-color: #0c0000;
    margin-top: 10px;
  }
  
  .progress {
    height: 100%;
    background-color: #a5d698;
  }
  
  button {
    margin: 5px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #28282e;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
    position: relative;
    overflow: hidden;
  }
  
  button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(93, 82, 82, 0.3);
    transition: transform 0.3s ease;
    transform: scaleX(0);
    transform-origin: left;
    z-index: 1;
  }
  
  button:hover::before {
    transform: scaleX(1);
  }
  
  button:hover {
    background-color: #151c45;
    transform: scale(1.05);
  }
  
  button:active {
    transform: scale(0.95);
  }
  
  button span {
    position: relative;
    z-index: 2;
  }
  